<template>
  <div class="reportInfo">
    <mt-header :title="this.zdbh" class="cb-header">
       <mt-button icon="back"></mt-button>
       <router-link to="" slot="left">
         编号:
      </router-link> 
    </mt-header>
    <!-- 基础信息 -->
    <div class="baseNews">
     <div class="baseNav clearfix">
      <div class="basenewsImg">
        <img src="../../assets/cb-img/drawable-xxhdpi/baseNews.png" alt="">
      </div>
      <div class="basenewsTitle">
         基础信息
      </div>
      <div class="line">
        <img src="../../assets/cb-img/drawable-xxhdpi/line.png" alt="">
      </div>
     </div>
     <div class="base-content clearfix">
        <mt-field label="昵称:" type="text" v-model="zbjc" disabled></mt-field>
      <div class="dwdm">
        <mt-field label="所属单位:" type="text" v-model="dwdm" disabled></mt-field>
      </div>
      <div class="zbjc">
        <mt-field label="所属分队:" type ="text" v-model="ssld" disabled></mt-field>
      </div>
      <div class="dwdm">
        <mt-field label="累计里程:" type="text" v-model="AccumulDistance" disabled></mt-field>
      </div>
      <div class="zbjc">
        <mt-field label="累计油消耗:" type ="text" v-model="AccumulOil" disabled></mt-field>
      </div>
      <div class="dwdm">
        <mt-field label="累计小时: " type="text" v-model="AccumulCtMotoHour" disabled></mt-field>
      </div>
      <div class="zbjc">
        <mt-field label="发动机小时:" type ="text" v-model="AccumulFdjMotoHour" disabled></mt-field>
      </div>
      

     </div>
    </div>
    <!-- 信息采报 -->
    <div class="newsReport">
      <div class="newsReportNav clearfix">
        <div class="basenewsImg">
          <img src="../../assets/cb-img/drawable-xxhdpi/newscb.png" alt="">
        </div>
        <div class="basenewsTitle">
          信息采报
        </div>
         <div class="line">
        <img src="../../assets/cb-img/drawable-xxhdpi/line.png" alt="">
      </div>
      </div>
      <div class="newsReport-content">
        <form action="" onsubmit="return false;">
          <div class="newstab clearfix">
            <!-- <div class="newstabTitle">任务类型</div> " -->
            <div class="newstabnav clearfix">
              <ul v-for="(item, index) in options" :key="index" class="newsRadioList" >
                <li class="newsRadio" :class="{tabSectel:changeRed == index}" @click="check(item.value,index)">
                  <span>{{item.label}}</span>
                </li>
              </ul>
            </div>
          </div>
          <div class="news-content clearfix">
            <div class="beginTime fl">
              <mt-field label="开始时间" placeholder="开始时间" type="date" v-model="setBeginTime"></mt-field>
            </div>
            <div class="endTime rl">
              <mt-field label="结束时间" placeholder="结束时间" type="date" v-model="setEndTime"></mt-field>
            </div>
            <div class="beginTime fl">
              <mt-field label="行驶里程" placeholder="请输入行驶里程" type="text" v-model="distance"><span class="newsName">公里</span><span class="tipInput">*</span></mt-field>
            </div>
            <div class="endTime rl">
              <mt-field label="油耗" placeholder="300内的整数" type="text" v-model="oil"><span class="newsName">升</span><span class="tipInput">*</span></mt-field>
            </div>
            <div class="clearfix">
              <div class="beginTime fl">
              <mt-field label="车体小时" placeholder="小于50的整数" type="text" v-model="ctMotoHour"><span class="newsName">小时</span><span class="tipInput">*</span></mt-field>
              </div>
              <div class="endTime rl">
                <mt-field placeholder="60以内的整数" type="text" v-model="ctMotoMinute"><span class="newsName">分钟</span><span class="tipInput">*</span></mt-field>
              </div>
              <div class="beginTime fl">
                <mt-field label="发动机小时" placeholder="小于50的整数" type="text" v-model="fdjMotoHour"><span class="newsName">小时</span><span class="tipInput">*</span></mt-field>
              </div>
              <div class="endTime rl">
                <mt-field placeholder="60以内的整数" type="text" v-model="fdjMotoMinute"><span class="newsName">分钟</span><span class="tipInput">*</span></mt-field>
              </div>
            </div>
           </div> 
          <div class="clearfix">
             <div class="newsSubmit">
              <mt-button style="background:#56b27d;color:#fff;width:20%;height:10%;padding:1%;" @click="cbNewsSubmit">提交</mt-button>
            </div>
          </div>
           <div class="fillMore">
             <div class="fillMoreTitle">
               <p>填报更多>></p>
             </div>
           </div>
        </form>
      </div>
    </div>
    <!-- 历史统计 -->
    <div class="Historical-statistics">
      <div class="newsReportNav clearfix">
          <div class="basenewsImg">
            <img src="../../assets/cb-img/drawable-xxhdpi/statistics_icon.png" alt="">
          </div>
          <div class="basenewsTitle">
            历史统计
          </div>
           <div class="line">
        <img src="../../assets/cb-img/drawable-xxhdpi/line.png" alt="">
      </div>
      </div>
      <div class="historical-content clearfix">
        <div class="historical-pie beginTime fl">
          <div id="pieChart" class="chart-box" style="width:100%;height:300px;"></div>
        </div>
        <div class="historical-zhu endTime rl">
          <div id="barChart" class="chart-box" style="width:100%;height:300px;"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.news-content-date li{
  width: 50%;
  padding:2% 2%;
  font-size: 0.5rem;
  float: left;
}
.news-content-date li span{
  display: inline-block;
  text-align: left;
}
.news-content-date li span.news-content-date-title{
   width: 30%;
}
.news-content-date li span.news-content-date-num{
   width: 70%;
   padding: 0 1.8%;
}
.tabSectel {
  background: #67b789;
  color: #fff;
}
.Accumul-Historical {
  display: inline-block;
  width: 50%;
  padding: 3%;
  text-align: left;
  font-size: 10px;
}
.chart-box {
  height: 100%;
  width: 100%;
}
.cb-header {
  background: #316e31;
}
.fillMore {
  padding: 1%;
  /* background: #e4e4e4; */
  text-align: right;
  font-size: 12px;
  color: #999;
}
.tipInput {
  color: red;
  font-size: 16px;
}
.newsName {
  font-size: 10px;
}
.fl {
  float: left;
}
.rl {
  float: right;
}
.beginTime,
.endTime {
  width: 50%;
}
.newsRadioList li {
  width: 20%;
  height: 20%;
  padding: 7px 0;
  float: left;
  /* background: #26a2ff; */
  font-size: 12px;
  /* color: #666666; */
  border-radius: 5%;
  /* border-right: 1px solid #fff; */
}
/* .newsRadioList li:hover{
   background: #67b789;
   color: #fff;
} */
.newstab {
  margin-top: 2%;
}
.newstabTitle {
  width: 20%;
  float: left;
  text-align: center;
  padding: 7px 0;
  font-size: 12px;
}
.newstabnav {
  width: 100%;
  /* float: right; */
  border: 1px solid #38a64f;
  border-radius: 3px;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
.dwdm {
  float: left;
  width: 50%;
}
.zbjc {
  float: right;
  width: 50%;
}
.baseNav {
  /* background: #ccc; */
  width: 100%;
  padding: 2%;
}
.newsReportNav {
  /* background: #ccc; */
  width: 100%;
  padding: 2%;
}
.basenewsImg {
  width: 5%;
  height: 5%;
  float: left;
}
.basenewsImg img {
  width: 20px;
  height: 20px;
}
.basenewsTitle {
  float: left;
  width: 93%;
  text-align: left;
  padding-left: 2%;
  color: #56b27d;
}
</style>
<script>
import moment from 'moment/moment'
import {
  getCbNewsApi,
  getmotoHourByMonthApi,
  getmotoHourByWeekApi,
  getAccumulHistoricalStatisticsApi,
  gettestApi
} from "@/api/code";
import header from "../layout/header";
let echarts = require("echarts");
export default {
  components: {
    wxheader: header
  },
  data() {
    return {
      //任务类型切换时改变样式
      changeRed: -1,
      //累计信息统计
      AccumulDistance: "",
      AccumulOil: "",
      AccumulCtMotoHour: "",
      AccumulFdjMotoHour: "",
      //保存提交信息
      beginTime: new Date(),
      endTime: new Date(),
      distance: "",
      oil: "",
      ctMotoHour: "",
      ctMotoMinute: "",
      fdjMotoHour: "",
      fdjMotoMinute: "",
      fillDate: "",
      zbjc: "",
      dwdm: "",
      zbid: "",
      // 新加的两个字段
      zdbh: "", // 装备编号
      ssld: "",
      value: "", //当前任务类型的值
      //任务类型列表
      options: [
        {
          label: "非ZZJS行动",
          value: "非ZZJS行动"
        },
        {
          label: "HC训练",
          value: "HC训练"
        },
        {
          label: "YX训练",
          value: "YX训练"
        },
        {
          label: "ZY训练",
          value: "ZY训练"
        },
        {
          label: "JC训练",
          value: "JC训练"
        }
      ],
      //饼图的数据
      result: ""
    };
  },
  computed:{
    setBeginTime:function(){
      // console.log(this.beginTime.getFullYear()+"-"+"0"+(this.beginTime.getMonth()+1)+"-"+"0"+this.beginTime.getDate())
      return moment(this.beginTime).format('YYYY-MM-DD')
      // console.log(this.dateTimeBeginTimeShow());
      // return this.dateTimeBeginTimeShow();
    },
    setEndTime:function(){
      return moment(this.endTime).format('YYYY-MM-DD');
    }
  },
  created() {
    this.checkllq();
    // 加载基础信息
    this.cbBaseInfo();
    this.AccumulHistoricalStatistics();
    // 获取url中的信息
  },
  mounted() {
    //加载饼图
    this.historiyPie();
    //加载柱状图
    this.historyBar();
    this.motoHourByWeek();
    // 获取url中的信息
    // this.getObjectFromUrl();
    // this.AccumulHistoricalStatistics();
  },
  methods: {
    // dateTimeBeginTimeShow(){
    //     return this.beginTime.getFullYear()+"-"+"0"+(this.beginTime.getMonth()+1)+"-"+"0"+this.beginTime.getDate()
    // },
    //点击获取当前任务类型的方法
    check(data, index) {
      this.value = data;
      this.changeRed = index;
    },
    //判断是从pc端打开还是从移动端打开
    checkllq() {
      var userAgentInfo = navigator.userAgent;
      var Agents = new Array(
        "Android",
        "iPhone",
        "SymbianOS",
        "Windows",
        "iPad",
        "iPod"
      );
      var flag = true;
      for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          console.log(Agents[v]);
          //如果是从pc端打开
          if (Agents[v] == "Windows") {
            return this.getRequestWindow();
            //如果是从移动端打开
          } else if (
            Agents[v] == "Android" ||
            Agents[v] == "iPhone" ||
            Agents[v] == "SymbianOS" ||
            Agents[v] == "iPad" ||
            Agents[v] == "iPod"
          ) {
            return this.getRequest();
          }
          flag = false;
          break;
        }
      }
      return flag;
    },
    // 获取页面url中从pc端传递的参数
    getRequestWindow() {
      var url = window.location.href;
      url = decodeURI(url);
      var theRequest = new Object();
      if (url.indexOf("?") != -1) {
        var str = url.substr(0);
        var strs = str.split("&amp%3B");
        //  var strs = str.split("&");
        for (var i = 0; i < strs.length; i++) {
          theRequest[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
        }
      }
      return theRequest;
    },
    // 获取页面url中从移动端传递的参数
    getRequest() {
      var url = window.location.href;
      url = decodeURI(url);
      var theRequest = new Object();
      if (url.indexOf("?") != -1) {
        var str = url.substr(0);
        // var strs = str.split("&amp%3B");
        var strs = str.split("&");
        for (var i = 0; i < strs.length; i++) {
          theRequest[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
        }
      }
      return theRequest;
    },
    // 从url中获取字符串
    // getObjectFromUrl() {
    //   var url = window.location.href;
    //   url = decodeURI(url);
    //   let objectStr = JSON.parse(this.getStr(url, "object="));
    //   this.dwdm = objectStr.dwdm; // 单位代码
    //   this.zbjc = objectStr.zbjc; // zb简称
    //   this.zbid = objectStr.zbid; // zbID
    //   this.zdbh = objectStr.zdbh; // zb编号
    //   this.ssld = objectStr.ssld; // 所属分队
    // },
    // 截取指定字符串
    /*
      string 字符串;
      str 指定字符;
      split(),用于把一个字符串分割成字符串数组;
      split(str)[0],读取数组中索引为0的值（第一个值）,所有数组索引默认从0开始;
    */
    // getStr(string, str) {
    //   var str_after = string.split(str)[1];
    //   str_after = str_after.replace(/%3A/g, ":");
    //   return str_after;
    // },
    //基础信息方法
    cbBaseInfo() {
      var theRequest = this.checkllq();
      // var theRequest = this.getRequest();
      this.beforeLoad(theRequest);
    },
    //将url传递的参数信息
    beforeLoad(data) {
      
      // 参数信息
      this.dwdm = data.jldw; // 单位代码
      this.zbjc = data.zbjc; // 装备简称
      this.zbid = data.zbid; // 装备ID
      this.zdbh = data.zdbh; // 装备编号
      this.ssld = data.ssld; // 所属分队
      // this.zdbh = "--"; // 装备编号
      // this.ssld = "--"; // 所属分队
    },
    //表单验证
    ////验证300以内的数值
    getThreeHundred(n){
    return /^(?:[1-2]?[0-9]?[0-9])$/.test(n);
    },
    //验证60以内的数值
    getSixty(n){
      // return /^(?:[1-9]|1[0-9]|59)$/.test(n);
      return /^([1-6])?[0-9]$/.test(n);
    },
    //验证50以内的数值
    getFifty(n){
      // return /^(?:[1-9]|1[0-9]|49)$/.test(n);
      return /^([1-5])?[0-9]$/.test(n);
    },
    VerificaForm() {
      if (this.value == "") {
        this.$messagebox("提示", "请填写任务类型");
        return false;
      }
      if (this.distance == "") {
        this.$messagebox("提示", "请填写行驶里程");
        return false;
      }
      if ((this.oil == "") || this.getThreeHundred(this.oil) == false) {
        this.$messagebox("提示", "请填写油耗为300以内的数值");
        return false;
      }
      if (this.ctMotoHour == "" || (this.getFifty(this.ctMotoHour) == false)) {
        this.$messagebox("提示", "请填写车体小时小于50的数值");
        return false;
      }
      if (this.ctMotoMinute == "" || (this.getSixty(this.ctMotoMinute) == false)) {
        this.$messagebox("提示", "请填写车体分钟为60以内的数值");
        return false;
      }
      if (this.fdjMotoHour == "" || (this.getFifty(this.fdjMotoHour) == false)) {
        this.$messagebox("提示", "请填写发动机小时小于50的数值");
        return false;
      }
      if (this.fdjMotoMinute == "" || (this.getSixty(this.fdjMotoMinute) == false)) {
        this.$messagebox("提示", "请填写发动机分钟为60以内的数值");
        return false;
      }
      return true;
    },
    //采报信息提交
    cbNewsSubmit() {
      if (this.VerificaForm()) {
        this.formSubmit();
      }
    },
    //采报信息提交
    formSubmit() {
      var ctMotoHour = 
        parseInt(this.ctMotoHour)*60+ parseInt(this.ctMotoMinute);
      var fdjMotoHour =
        parseInt(this.fdjMotoHour)*60 + parseInt(this.fdjMotoMinute);
      var beginTime = new Date(this.beginTime).getTime();
      var endTime = new Date(this.endTime).getTime();
      var fillDate = new Date().getTime();
      getCbNewsApi(
        this.zbid,
        ctMotoHour,
        fdjMotoHour,
        this.oil,
        fillDate,
        beginTime,
        endTime,
        this.distance,
        this.value
      )
        .then(req => {
          console.log(req);
          if (req.data) {
            this.$messagebox("提示", "提交成功！");
            this.motoHourByWeek();
            this.AccumulHistoricalStatistics();
            this.historiyPie();
          } else {
            this.$messagebox("提示", "提交失败，请重新填报！");
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //页面加载柱状图的信息
    motoHourByWeek() {
      getmotoHourByWeekApi(new Date().getTime(), this.zbid)
        .then(req => {
          this.historyBar(req.data);
        })
        .catch(error => {
          console.log(error);
        });
    },
    //点击当前当前扇区执行的方法
    currentPieBootsector(param) {
      getmotoHourByWeekApi(param.data.date, this.zbid)
        .then(req => {
          console.log(req.data);
          this.historyBar(req.data);
        })
        .catch(error => {
          console.log(error);
        });
    },
    //绘制饼图
    historiyPie() {
      let historiyPieChart = echarts.init(document.getElementById("pieChart"));
      var res = [];
      //点击饼图当前扇区
      historiyPieChart.on("click", this.currentPieBootsector);
      let historiyPieoption = {
        title: {
          text: "单车摩托小时近一个月消耗情况",
          // subtext: "纯属虚构",
          x: "center",
          textStyle: {
            //标题内容的样式
            color: "#56b27d", //京东红
            fontStyle: "normal", //主标题文字字体风格，默认normal，有italic(斜体),oblique(斜体)
            fontWeight: "lighter", //可选normal(正常)，bold(加粗)，bolder(加粗)，lighter(变细)，100|200|300|400|500...
            fontFamily: "san-serif", //主题文字字体，默认微软雅黑
            fontSize: 12 //主题文字字体大小，默认为18px
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c}h"
        },
        legend: {
          orient: "vertical",
          left: "left",
          bottom: 5,
          itemGap: 5,
          itemWidth: 10,
          textStyle: {
            fontSize: 8
          },
          data: ["本周", "上一周", "上两周", "上三周"]
        },
        series: [
          {
            name: "车体小时",
            type: "pie",
            radius: "55%",
            center: ["65%", "60%"],
            data: res,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            },
            label: {
              fontSize: 8,
              width: 10
            },
            labelLine: {
              length: 0.5
            }
          }
        ]
      };
      historiyPieChart.setOption(historiyPieoption);
      //  onResize
      window.addEventListener("resize", () => {
        historiyPieChart.resize();
      });
      var day = new Date().getTime();
      getmotoHourByMonthApi(day, this.zbid)
        .then(req => {
          console.log("饼图");
          console.log(req);
          var result = req.data;
          for (var i = 0; i < result.length; i++) {
            res.push({
              //通过把result进行遍历循环来获取数据并放入Echarts中
              value: result[i].motoHour,
              name: result[i].week,
              date: result[i].date
            });
          }
          historiyPieChart.setOption(historiyPieoption);
        })
        .catch(error => {
          console.log(error);
        });
    },
    //绘制柱状图
    historyBar(data) {
      let historiyBarChart = echarts.init(document.getElementById("barChart"));
      var xAxisData = [];
      var yAxisData = [];
      let historiyBaroption = {
        color: ["#3398DB"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            splitLine: { show: false }, //去除网格线
            type: "category",
            axisLabel: {
              interval: 0,
              rotate: 40
            },

            data: xAxisData,
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            splitLine: { show: false }, //去除网格线
            type: "value",
            name: "车体小时/h"
          }
        ],
        series: [
          {
            name: "车体小时",
            type: "bar",
            barWidth: "60%",
            data: yAxisData
          }
        ],
        label: {
          normal: {
            show: true
            // position:
          }
        }
      };
      historiyBarChart.setOption(historiyBaroption);
      for (var key in data) {
        xAxisData.push(data[key].date);
        yAxisData.push(data[key].motoHour);
      }
      historiyBarChart.setOption(historiyBaroption);
      //onResize
      window.addEventListener("resize", () => {
        historiyBarChart.resize();
      });
    },
    //累计加载历史统计车体信息
    AccumulHistoricalStatistics() {
      console.log("id");
      console.log(this.zbid);
      getAccumulHistoricalStatisticsApi(this.zbid)
        .then(req => {
          this.AccumulDistance = parseFloat(req.data.distanceSum).toFixed(2)+"公里"; //里程
          this.AccumulOil =parseFloat(req.data.oilSum).toFixed(2)+"升"; // 油量
          this.AccumulCtMotoHour = parseFloat(req.data.ctMotoHourSum / 60).toFixed(2)+"小时"; // 摩托小时
          this.AccumulFdjMotoHour =parseFloat( req.data.fdjMotoHourSum / 60).toFixed(2)+"小时"; // 发动机
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>





