<template>
  <div class="main">
    <wxheader></wxheader>
    <div class="content">
      <div class="content-one">
          <mt-field label="test1" placeholder="请输入test1" type = "text" v-model="name1"></mt-field>
          <mt-field label="test2" placeholder="请输入test2" type="text" v-model="name2"></mt-field>
          <mt-field label="test3" placeholder="请输入test3" type="text" v-model="name3"></mt-field>
          <mt-field label="组织机构ID" placeholder="请输入test4" type="text" v-model="organizationId"></mt-field>
          <mt-button type="primary"  @click.native="changeData">编辑</mt-button>
      </div>
     <div class="content-two">
          <mt-field label="摩托小时" placeholder="请输入摩托小时" type = "text" v-model="motoHour"></mt-field>
          <mt-field label="摩托里程" placeholder="请输入摩托里程" type="text" v-model="motoDistance"></mt-field>
          <mt-field label="动用次数" placeholder="请输入动用次数" type="text" v-model="useNumber"></mt-field>
          <mt-button type="primary"  @click.native="reportNews">采报</mt-button>
     </div>
    </div>
  </div>
</template>
<style scoped>
  .main{
    width: 100%;
    height: 100%;
    /* background: #000; */
  }
  .content{
    margin-top: 20%;
  }
  /* .code-img{
    display: none;
  } */
 
</style>
<script>
import header from '../layout/header'
import{getCodeDetailApi,getCodeDetailChangeApi,getReportNewsApi}from '@/api/code'
import QRCode from 'qrcodejs2'

export default {
  components:{
    'wxheader': header,
    QRCode
  },
  data(){
    return {
      code:false,
      name1:'',
      name2:'',
      name3:'',
      organizationId:'',
      equipmentId:'',
      motoHour:'',
      motoDistance:'',
      useNumber:'',
      impListData: {
      config: {
        value: 'https://www.baidu.com',
        imagePath: 'imagePath',
        filter: 'color',
        size: 500
      },
      downloadButton: true
 }
    }
  },
  created(){
    this. info();
  },
  mounted(){
  },
  methods:{
    info(){
      var veId = window.location.href.split("=")[1];
      console.log(veId);
      getCodeDetailApi(veId).then(req =>{
        console.log(req);
        this.name1 = req.data.name1;
        this.name2 = req.data.name2;
        this.name3 = req.data.name3;
        this.organizationId = req.data.organizationId;
        this.equipmentId = req.data.equipmentId;
      }).catch(error =>{
        console.log(error);
      });
    },
    changeData(){
      console.log(this.name1);
      console.log(this.name2);
      console.log(this.name3);
      console.log( this.organizationId);
      console.log( this.equipmentId);
      getCodeDetailChangeApi(this.name1,this.name2,this.name3,this.organizationId,this.equipmentId)
      .then(req => {
         console.log(req)
        if(req){
            this.$MessageBox({   title: '提示',  message: '编辑成功',  showCancelButton: true });
        }else
        {
           this.$MessageBox({   title: '提示',  message: '未编辑成功',  showCancelButton: true });
        }
        console.log("显示信息");
          //  this.$nextTick (function () {
          //  var baseUrl = window.location.host;
          //  this.qrcode(req.data);
          //   // this.qrcode(baseUrl+"/#/detail");
          // })
      })
      .catch(error =>{
        console.log("请检查网络情况");
        console.log(error);
      })
    },
    reportNews(){
      console.log(this.motoHour);
      console.log(this.motoDistance);
      console.log(this.useNumber);
      console.log(this.equipmentId);
      getReportNewsApi(this.motoHour,this.motoDistance,this.useNumber,this.equipmentId)
      .then(req=>{
         console.log(req);
        if(req){
          this.$MessageBox({   title: '提示',  message: '采报信息已保存',  showCancelButton: true });
        }else{
           this.$MessageBox({   title: '提示',  message: '采报信息未保存成功',  showCancelButton: true });
        }

      })
      .catch(error =>{
        console.log(error)
      });
    }
  }
}
</script>





