import axios from '@/lib/http.js'
/**
 * 
 * @param name1
 * @param name2
 * @param name3
 * @param organizationId
 */
//首页
export function getCodeApi(name1,name2,name3,organizationId) {
  return axios({
    method: 'post',
    url:"http://172.16.1.39:8080/save",
    url:"/save",
    // url:"/save",
    data:{
      name1,name2,name3,organizationId
    }
  })
}
//详情页
export function getCodeDetailApi(id) {
  return axios({
    method: 'get',
    url:"http://172.16.1.39:8080/find/"+id,
    // url:"/find/"+id,
  })
}


/**
 * 
 * @param name1
 * @param name2
 * @param name3
 * @param organizationId
 * @param equipmentId
 */
//详情页信息编辑
export function getCodeDetailChangeApi(name1,name2,name3,organizationId,equipmentId) {
  return axios({
    method: 'get',
    // url:"http://172.16.1.39:8080/update",
    url:"/update",
   params:{
    name1,name2,name3,organizationId,equipmentId
   }
  })
}

/**
 * 
 * @param motoHour
 * @param motoDistance
 * @param useNumber
 * @param equipmentId
 */
//获取采报信息
export function getReportNewsApi(motoHour,motoDistance,useNumber,equipmentId) {
  return axios({
    method: 'get',
    url:"http://172.16.1.39:8080/saveCollection",
    // url:"/saveCollection",
    params:{
      motoHour,
      motoDistance,
      useNumber,
      equipmentId
    }
  })
}

