<template>
  <div class="main">
    <wxheader></wxheader>
    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref='loadmore' class="clearfix">
      <ul class="listBase fl">
        <li v-for="(item, index) in proCopyright" :key="item.zbid" class="clearfix listBaseNew">
          <div class="zb-checkbox">
             <!-- <mt-checklist
              v-model="value"
              :options="options">
            </mt-checklist> -->
          </div> 
          <div class="zb-news">
            <mt-field label="装备单位" placeholder="请输入装备单位" type ="text" v-model="item.dwdm" disabled></mt-field>
            <mt-field label="装备ID" placeholder="请输入装备ID" type="text" v-model="item.zbid" disabled></mt-field>
              <mt-field label="装备简称" placeholder="请输入装备简称" type="text" v-model="item.zbjc" disabled></mt-field>
          </div>
        </li>
      </ul>
      <ul class="listCode rl">
          <li class="listBaseNewCode"  v-for ="item in valueDate" :key="item">
            <!-- 生成二位码 -->
            <qrcode-vue :value="item" :size="size" level="H"></qrcode-vue>
        </li>
      </ul>
    </mt-loadmore>
  </div>
</template>
<style scoped>
.fl{
  float: left;
}
.rl{
  float: right;
}
.listBase{
  width: 70%;

}
.listCode{
  width: 30%;
}
.clearfix:after{
  content:"";
  height:0;
  line-height:0;
  display:block;
  visibility:hidden;
  clear:both;
}
  .main{
    width: 100%;
    height: 100%;
    /* background: #000; */
  }
  .content{
    margin-top: 20%;
  }
  ul,li{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .listBaseNewCode{
    padding: 30px 10px 30px 0px;
    background-color: #fff;
    margin-bottom: 10px;
    /* clear: both; */
    width: 100%;
    /* height: 137px; */
    border-bottom: 1px solid #ccc;
  }
  .listBaseNew{
    padding: 10px 0;
    background-color: #fff;
    margin-bottom: 10px;
    /* clear: both; */
    width: 100%;
    /* height: 137px; */
    border-bottom: 1px solid #ccc;
    }
    .zb-checkbox{
      float: left;
      width: 10%
    }
    .zb-news{
      float: left;
      width: 60%;
    }
    .zb-code{
      float: right;
      width: 30%;
     
    }
    /* .zb-code .zb-code-btn{
      padding-top: 100%;
    } */
</style>
<script>
import header from './header'
import{getlistEquipment}from '@/api/code'
// import QRCode from 'qrcodejs2'
import QrcodeVue from 'qrcode.vue';
export default {
  components:{
  'wxheader': header,
  QrcodeVue
  //  QRCode
  },
  data(){
    return {
      valueDate: '',
      size: 100,
      // qrcodeId:'',//获取动态二维码ID
      pageNo:1,//显示当前页数
      pageSize:10,//显示每页条数
      proCopyright:[],
      allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
      scrollMode:"auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
      totalpage:'',
      loading:false,
      bottomText: '',
      JLDW:1,
      options:''
  
    }
  },
  created(){
    this.loadPageList();
    // this.handleClick();
  },
  mounted(){

  },
  methods:{
    // 上拉加载功能
    loadBottom(){
      console.log("上拉");
      this.more();//上拉触发的加载功能
      this.$refs.loadmore.onBottomLoaded();//固定方法，查询完调用一次，用于重新定位
    },
    loadPageList(){
      // 查询数据
      getlistEquipment(this.pageNo,this.pageSize)
      .then(res =>{
        console.log("显示信息");
        console.log(res);
        this.proCopyright = res.data.content;
        var urlDate = [];
        for(var i=0;i<this.proCopyright.length;i++){
          this.zbid = this.proCopyright[i].zbid;// 装备ID
          this.dwdm = this.proCopyright[i].dwdm;// 单位代码
          this.zbjc = this.proCopyright[i].zbjc;// 装备简称
          this.zdbh = this.proCopyright[i].zdbh;//装备编号
          this.ssld = this.proCopyright[i].ssld;//所属分队

          // this.zdbh = "--";//装备编号 +"&zdbh"+this.zdbh+"&ssld"+this.ssld
          // this.ssld = "--";//所属分队
          let baseUrl = window.location.origin;
          // let url=baseUrl+"/#/report_Info?zbid="+this.zbid+"&dwdm="+this.dwdm+"&zbjc="+this.zbjc+"&zbid="+this.zbid+"&zdbh="+this.zdbh+"&ssld="+this.ssld;
          
          let url="http://www.wx-jss.com:8080/#/report_Info?zbid="+this.zbid+"&jldw="+this.dwdm+"&zbjc="+this.zbjc+"&zbid="+this.zbid+"&zdbh="+this.zdbh+"&ssld="+this.ssld;
          // console.log(url);
          // let url=baseUrl+"/#/report_Info?zbid="+this.zbid
          //        +`&object={"zbid":"${this.zbid}","dwdm":"${this.dwdm}","zbjc":"${this.zbjc}","zdbh":"${this.zdbh}","ssld":"${this.ssld}"}`;
          // let url="http://www.wx-jss.com:8080/#/report_Info?zbid="+this.zbid
          //        +`&object={"zbid":"${this.zbid}","dwdm":"${this.dwdm}","zbjc":"${this.zbjc}","zdbh":"${this.zdbh}","ssld":"${this.ssld}"}`;
          urlDate.push(url);
        }
        this.valueDate = urlDate;
        this.totalpage = Math.ceil(res.data.totalElements/this.pageSize);
        console.log("获取总页数");
        console.log(this.totalpage);
        if(this.totalpage == 1){
          this.allLoaded = true;
        }
        this.$nextTick(function () {
          // 是否还有下一页，加个方法判断，没有下一页要禁止上拉
          this.scrollMode = "touch";
          this.isHaveMore();
        });
      }).catch(error =>{
        console.log(error);
        let baseUrl = window.location.origin;
        // window.open(baseUrl+"/#/error_505");
        // window.location.href = baseUrl+"/#/error_505";

      });
    },
    more(){
      // 分页查询
      if(this.totalpage == 1){
        this.pageNo = 1;
        this.allLoaded = true;
      }else{
        this.pageNo = parseInt(this.pageNo) + 1;
        console.log("加载更多");
        console.log(this.pageNo);
        this.allLoaded = false;
      }
      getlistEquipment(this.pageNo,this.pageSize).then(res =>{
        this.proCopyright = this.proCopyright.concat(res.data.content);
        var urlDate = [];
        for(var i=0;i<this.proCopyright.length;i++){
          this.zbid = this.proCopyright[i].zbid;
          this.dwdm = this.proCopyright[i].dwdm;
          this.zbjc = this.proCopyright[i].zbjc;
          this.zdbh = this.proCopyright[i].zdbh;
          this.ssld = this.proCopyright[i].ssld;
          // this.zdbh = "--";//装备编号  +"&zdbh"+this.zdbh+"&ssld"+this.ssld
          // this.ssld = "--";//所属分队
          let baseUrl = window.location.origin;
          //  let url=baseUrl+"/#/report_Info?zbid="+this.zbid+"&dwdm="+this.dwdm+"&zbjc="+this.zbjc+"&zbid="+this.zbid+"&zdbh="+this.zdbh+"&ssld="+this.ssld;
           let url="http://www.wx-jss.com:8080/#/report_Info?zbid="+this.zbid+"&jldw="+this.dwdm+"&zbjc="+this.zbjc+"&zbid="+this.zbid+"&zdbh="+this.zdbh+"&ssld="+this.ssld;
          // console.log(url);
          // let url=baseUrl+"/#/report_Info?zbid="+this.zbid
          //        +`&object={"zbid":"${this.zbid}","dwdm":"${this.dwdm}","zbjc":"${this.zbjc}","zdbh":"${this.zdbh}","ssld":"${this.ssld}"}`;
          // let url="http://www.wx-jss.com:8080/#/report_Info?zbid="+this.zbid
          //        +`&object={"zbid":"${this.zbid}","dwdm":"${this.dwdm}","zbjc":"${this.zbjc}","zdbh":"${this.zdbh}","ssld":"${this.ssld}"}`;
          urlDate.push(url);
        }
        this.valueDate = urlDate;
        this.isHaveMore();
      })
    },
    isHaveMore:function(){
      // 是否还有下一页，如果没有就禁止上拉刷新
      this.allLoaded = false; //true是禁止上拉加载
      if(this.pageNo == this.totalpage){
        this.allLoaded = true;
      }
    }
  }
}
</script>





