import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Layout from '@/views/layout'
import codeDetail from '@/views/cdDetail'
import ReportInfo from '@/views/reportInfo/index' //采报信息详情
import error505 from '@/views/status/error505'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // }
    {
      path: '/',
      name: 'Layout',
      component: Layout
    },
    {
      path: '/detail',
      name: 'codeDetail',
      component: codeDetail
    },
    {
      path:'/report_Info',
      name:'ReportInfo',
      component:ReportInfo
    },{
      path:'/error_505',
      name: 'error505',
      component:error505
    }
  ]
})



